.panelExitButton p {
    color: white;
    cursor: pointer;
    width: 25px;
    height: 25px;
    position: absolute;
    right: 0em;
    top: 0em;
    text-align: center;
    border: 1px solid $brand-orange;
    border-radius: 0 0 0 4px;
    background: $brand-orange;
    margin: 0em;
}

#summaryPanel {
    width: 800px;
    margin-top: -317px;
