.panel {
    display: none;
    background-color: white;
    position: fixed;
    top: 50%;
    left: 50%;
    border: 1px solid $brand-blue;
    border-top-color: $toolbox-btn-active;
    z-index: 1002;
}

.panel .content {
    padding: 0 15px 15px 15px;
    background: white;
    cursor: default;
}

.panel .description {
    padding: 5px;
    background-color: $brand-blue;
    color: $white;
}

.panel .description h4 {
    color: white;
    margin: 0;
    text-align: center;
}

table {
    overflow: auto;
    width: 100%;
}

table, th, td {
    border-collapse: collapse;
}

th, td {
    text-align: center !important;
    padding: 3px !important;
    height: 2em;
}

.table-scroll {
    overflow: auto;
    height: 25vh;
    padding-bottom: 1vh;
}

#chart {
    height: 40vh;
    width: 770px;
    background-color: $brand-gray;
}

.draggable {
    opacity: 1;
}
